import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col,
  Modal,
  DatePicker,
  Input
} from 'antd'
import {Import} from 'HOC'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'
import {API_FRMLOSSORDERS_IMPORT} from 'api'
import moment from 'moment'

@connect(
  state => ({...state.reportLossManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
      visible: false
    }
  }

  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    console.log(MEUN)
    const ID = getNowPageId(MEUN, window.location.pathname)
    console.log(ID)
    GetButtonPower({urlParams: [ID]})
  }
  // 执行
  handleCarried = () => {
    const {selectedRows, handleCarriedOut, handleGetList, size, update} = this.props
    let req = []
    selectedRows.map((o) => {
      req.push(o.orderCode)
    })
    if (selectedRows.length === 0) {
      message.error('请选择一条记录进行执行操作')
      return
    }
    if (req.length > 1) {
      message.error(' 只能选择一条记录进行执行操作，请确认！')
    } else {
      handleCarriedOut({data: {orderCodes: req}}, (data) => {
        setTimeout(() => {
          update({tableLoading: false})
          message.success('执行成功!')
          handleGetList({searchParams: {page: 1, size}})
        }, 5000)
      })
    }
  }

  //取消
  Cancel = () => {
    const {selectedRows, handleCancel, handleGetList, size} = this.props
    let req = []
    selectedRows.map((o) => {
      req.push(o.orderCode)
    })
    if (selectedRows.length === 0) {
      message.error('请选择一条记录进行取消操作')
      return
    }
    if (selectedRows.length > 1) {
      message.error('只能选择一条记录进行取消操作，请确认！')
    } else {
      handleCancel({data: {orderCodes: req}}, (data) => {
        (data.length === 0) ? message.success('取消成功') : message.error(`${data}不处于可取消状态`),
          handleGetList({searchParams: {page: 1, size}})
      })
    }
  }

  //导出
  export = () => {
    const {handleExport, searchParams, userLoation, page, size} = this.props
    handleExport({searchParams: {...searchParams, warehouseCode: userLoation.value, page, size}}, () => {
      message.success('导出成功')
    })
  }

  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    console.log(downloadData)
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {update, handleGetList, searchParams, page, size} = this.props
    console.log(res)
    update({tableLoading: false})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

  // // 我知道了
  // handleExprotErrorCancel=()=>{
  //   const {update} = this.props
  //   // update({isShowFullModal: false})
  // }

    // 冲销功能
  writeOff = () => {
    this.setState({visible: true})
    const {update} = this.props
    update({writeOff: {}})
  }

    // 确认冲销
  okModal = () => {
    const {writeOff, userLoation, handleFrmFix} = this.props
    if (!writeOff.postTime) {
      message.error('请先选择记账日期')
      return
    }
    if (!writeOff.orderCode) {
      message.error('请输入单号')
      return
    }
    handleFrmFix({searchParams: {postTime: moment(writeOff.postTime).format('YYYY-MM-DD'), warehouseCode: userLoation.value, orderCode: writeOff.orderCode}}, () => {
      console.log('1111')
      const {handleGetList, searchParams, size} = this.props
      let reqObj = {...searchParams, warehouseCode: userLoation.value}
      handleGetList({searchParams: {...reqObj, page: 1, size}})
      message.success('冲销成功')
      this.setState({visible: false})
    })
  }

  cancelModal = () => {
    this.setState({visible: false})
  }

  onInputChange = (values) => {
    const {update, writeOff} = this.props
    console.log(values)
    update({writeOff: {...writeOff, ...values}})
  }

  onImport = () => {
    const {update} = this.props
    update({isShowRecordModal: true})
  }

  render() {
    const {reportLossCarryBottonPower} = this.props
    const {reportLossManagementWriteOff, writeOff: {postTime, orderCode}, buttonLoading} = this.props
    const dateFormat = 'YYYY-MM-DD'
    return (
      <div className={style['table-tool']}>
        <Row type="flex" justify="start">
          <Col span={12}>
            {
              reportLossCarryBottonPower && <Button type="primary" className={style.btn} onClick={this.handleCarried}>执行</Button>
            }
            <Button type="primary" className={style.btn} onClick={this.Cancel}>取消</Button>
            <Button type="primary" className={style.btn} onClick={this.export}>导出</Button>
            <Button type="primary" className={style.btn} onClick={() => {
              this.handleDownLoad('T08')
            }}>模板下载</Button>
            {/* <Import
              action={{api: API_FRMLOSSORDERS_IMPORT}}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const {update} = this.props
                update({tableLoading: true})
              }}
              onResponse={() => {
                const {update} = this.props
                update({tableLoading: false})
              }}
            >
            </Import> */}
              <Button type="primary" className={style.btn} onClick={this.onImport}>导入</Button>
            {
              reportLossManagementWriteOff && <Button type="primary" className={style.btn} onClick={this.writeOff}>冲销</Button>
            }
          </Col>
        </Row>
        <div>
          <Modal
            title="冲销确认"
            width={780}
            visible={this.state.visible}
            onOk={this.okModal}
            onCancel={this.cancelModal}
            okText="确认"
            confirmLoading={buttonLoading}
            cancelText="取消"
          >
            <Row>
              <Col span={12}>
                <div className="commonInput">
                  <label><span style={{color: 'red'}}>*</span>记账日期：</label>
                  <div className="box">
                    <DatePicker
                      value={postTime}
                      format={dateFormat}
                      selectItem={{data: postTime}}
                      onChange={(value, moment) => {
                        this.onInputChange({postTime: value})
                      }}
                    />
                  </div>
                </div>
              </Col>
              <Col span={12}>
                <div className="commonInput">
                  <label><span style={{color: 'red'}}>*</span>单号：</label>
                  <div className="box">
                    <Input value={orderCode} onChange={(e) => {
                      this.onInputChange({orderCode: e.target.value})
                    }
                    } />
                  </div>
                </div>
              </Col>
            </Row>
          </Modal>
        </div>
      </div>
    )
  }
}



