import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {update} from '../../storePriority/redux/action'

@connect(
  state => ({...state.reportLossManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '单号',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <a onClick={() => {
        const {history, history: {location}} = this.props
        window.open(`${location.pathname}/${record.orderCode}`)
        // history.push(`${location.pathname}/${record.orderCode}`)
      }}>{text}</a>
    }, {
      title: '报损类型',
      width: 150,
      dataIndex: 'frmlossTypeName',
      key: 'frmlossTypeName'
    }, {
      title: '报损金额（含税）',
      width: 150,
      dataIndex: 'frmlossTotalAmount',
      key: 'frmlossTotalAmount'
    }, {
      title: '报损金额（不含税）',
      width: 150,
      dataIndex: 'frmlossTotalAmountNoTax',
      key: 'frmlossTotalAmountNoTax'
    }, {
      title: '成本中心',
      width: 150,
      dataIndex: 'costCenterCode',
      key: 'costCenterCode',
      render: (text, record) => <span>{record.costCenterCode && `${record.costCenterCode}-${record.costCenterName}`}</span>
    }, {
      title: '备注',
      width: 150,
      dataIndex: 'remark',
      key: 'remark'
    }, {
      title: '状态',
      width: 150,
      dataIndex: 'statusDes',
      key: 'statusDes'
    }, {
      title: '创建人',
      width: 150,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '创建时间',
      width: 200,
      dataIndex: 'createTime',
      key: 'createTime'
    }]
  }

  //一些操作
  onEdit = (modalType, record) => {
    const {update, modalId} = this.props
    update({
      [modalType]: true,
      modalId: [record.id],
      orderCode: record.orderCode,
      shipperName: record.shipperName,
      frmlossTotalAmount: record.frmlossTotalAmount,
      frmlossTypeName: record.frmlossTypeName,
      remark: record.remark
    })
  }

  componentDidMount() {
    const {handleGetList, searchParams, page, size} = this.props
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, ...searchParams, size: pagination.pageSize}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    // rowSelection object indicates the need for row selection
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys,
      getCheckboxProps: record => ({
        disabled: record.status === 1 || record.status === 2 || record.status === 3
      })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          rowSelection={rowSelection}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 1150, y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



