import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Button,
  Input,
  message
} from 'antd'
import {connect} from 'react-redux'
import {Import} from 'HOC'
import {CsxSelect} from 'Form'
import {API_GETREQUISITIONSTTYPEKEYVALUE, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE, API_FRMLOSSORDERS_IMPORT} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reportLossManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
  }

  componentDidMount() {

  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowRecordModal: false, importModal: {}})
  }

  onInputChange(values) {
    const {importModal, update} = this.props
    update({importModal: {...importModal, ...values}})
  }

  handleBeforeImport = (file) => {
    const {update, importModal} = this.props
    let reqObj = {}
    reqObj = {...importModal}
    if (!reqObj.costCenterName) {
      message.error('请先选择成本中心')
      return reqObj
    }
    if (Object.prototype.toString.call(reqObj) === '[object Object]') {
      reqObj.costCenterCode = reqObj.costCenterName && reqObj.costCenterName.value
      reqObj.costCenterName = reqObj.costCenterName && reqObj.costCenterName.children
    }
    update({tableLoading: true, importData: {...reqObj}})
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const {update, handleGetList, searchParams, page, size} = this.props
    update({tableLoading: false, isShowRecordModal: false, importModal: {}})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

  // 我知道了
  handleExprotErrorCancel = () => {
    const {update} = this.props
    // update({isShowRecordModal: false})
  }

  render() {
    const {
      importModal: {
        costCenterName
      },

      importModal,
      importData
    } = this.props
    console.log(importModal.costCenterName)
    return (
      <Modal
        title="创建导入"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={860}
      >
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={24}>
              <CsxSelect
                dateApi={API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE}
                label='成本中心'
                require={true}
                selectItem={{data: costCenterName, key: 'key', valueKey: ['key', 'value']}}
                onChangeSelect={(value) => {
                  this.onInputChange({costCenterName: value})
                }}
              />
            </Col>
          </Row>
          <Row type="flex" justify="end" style={{marginTop: '15px'}}>
            <Col>
              <Import
                action={{api: API_FRMLOSSORDERS_IMPORT, data: {...importData}}}
                onOk={this.handleImportSuccess}
                onExprotError={this.handleExprotError}
                beforeImport={
                  this.handleBeforeImport
                }
                onExprotErrorCancel={this.handleExprotErrorCancel}
                onResponse={() => {
                  const {update} = this.props
                  update({tableLoading: false})
                }}
              >
                {
                  <Button type="primary" style={{marginRight: '84px', marginLeft: '10px'}}>导入</Button>
                }
              </Import>
            </Col>
          </Row>
        </div>
      </Modal>

    )
  }
}



