import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  Input,
  message
} from 'antd'
import {CsxSelect, CsxValidateInput, CsxFormSearch} from 'Form'
import {API_GETREQUISITIONSTTYPEKEYVALUE, API_STOCKRELOCATION_SHIPPERKEYVALUES, API_NEWPRODUCT_GETPRODUCTKEYVALUES, API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reportLossManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      key: 'caozuo',
      dataIndex: 'caozuo',
      render: (text, record, index) => <div className="tableOperation">
        {
          record.productCode ? <span className='red' onClick={() => {
            for (let key in record) {
              if (key == 'id') continue
              delete record[key]
            }
            this.forceUpdate()
          }}>清空</span> : null
        }
      </div>
    },
    {
      title: '商品编码',
      width: 250,
      dataIndex: 'productCode',
      key: 'productCode',
      render: (text, record, index) => {
        console.log('record is', record)
        const {userLoation} = this.props
        return (<CsxFormSearch
          dateApi={API_NEWPRODUCT_GETPRODUCTKEYVALUES}
          searchParams={{warehouseCode: userLoation.value}}
          searchParamsKey='productCode'
          selectItem={{data: record.productCode, key: 'key', valueKey: ['key', 'value']}}
          onChangeSelect={(value) => {
            record.productCode = value
            this.forceUpdate()
          }}
          onSelect={(value) => {
            record.productCode = value
            this.forceUpdate()
          }}
        />)
      }
    },
    {
      title: '储位编号',
      width: 200,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode',
      render: (text, record, index) => {
        const {userLoation, popData} = this.props
        return <CsxFormSearch
          dateApi={API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES}
          searchParams={{
            warehouseCode: userLoation.value,
            shipperCode: popData.shipperCode && popData.shipperCode.value,
            productCode: record.productCode && record.productCode.value
          }}
          searchParamsKey='storeLocationCode'
          selectItem={{data: record.storeLocationCode, key: 'storeLocationCode', valueKey: ['storeLocationCode', 'storeLocationName']}}
          onChangeSelect={(value) => {
            record.storeLocationCode = value
            this.forceUpdate()
          }}

          firstData={
            record.productCode === undefined ? '商品编码' : null
          }
          onFocusGetData={true}
          onFocus={(data) => {
            console.log(record.productCode)
            // if(!record.productCode) {
            //   message.error('请先输入商品编码')
            //   return
            // }
            // console.log(record.productCode,111111)
            // const{handleChuweiInfo, userLoation, searchParams} = this.props
            console.log(data)
            if (data.result.length === 0) {
              message.error(`${record.productCode.children}没有储位`)
            }
            // handleChuweiInfo({searchParams:{
            //   warehouseCode: userLoation.value,
            //   shipperCode: searchParams.shipperCode.value,
            //   productCode: record.productCode && record.productCode.value,
            //   // storeLocationCode: null
            // }}, (data)=>{
            //   console.log(record.productCode.children)
            //   if(data.length===0) {
            //     message.error(record.productCode.children+'没有储位')
            //   }
            // })
          }}
          onSelect={(value) => {
            const {handleChuweiInfo, userLoation, popData, update} = this.props
            handleChuweiInfo({
              searchParams: {
                warehouseCode: userLoation.value,
                shipperCode: popData.shipperCode.value,
                productCode: record.productCode && record.productCode.value,
                storeLocationCode: value.value
              }
            }, (data) => {
              record.unit = data[0].unit
              record.availableQty = data[0].availableQty
              this.forceUpdate()
              update({availableQty: data[0].availableQty, storeLocationCode: record.storeLocationCode})
            })
            record.storeLocationCode = value
          }}
        />
      }
    }, {
      title: '可用量',
      width: 150,
      dataIndex: 'availableQty',
      key: 'availableQty'
    }, {
      title: '报损数量',
      width: 150,
      key: 'frmlossQty',
      render: (text, record, index) => (
          <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) == 'KG' ? 'TWOO' : 'REGO'}
            value={record.frmlossQty ? record.frmlossQty.value == undefined ? record.frmlossQty : record.frmlossQty.value : ''}
            errormes = {record.productCode ? '' : '请先输入商品编码'}
            error={record.frmlossQty && record.frmlossQty.error || false} handleOnchange={(data) => {
              record.frmlossQty = data
              const {update} = this.props
              update({frmlossQty: record.frmlossQty, frmlossQtyStute: record.frmlossQty.error})
              this.forceUpdate()
            }} />)
      // render: (text, record, index) => <Input value={record.frmlossQty} style={{width: '100px'}} onChange={(e) => {
      //   // 处理领用数量抵扣可用量
      //   const{availableQty, update}=this.props
      //   let n = record.frmlossQty
      //   record.frmlossQty = e.target.value
      //   update({nowFrmlossQty: e.target.value})
      //   if(record.frmlossQty <= availableQty) {
      //     if(n !== e.target) {
      //       record.availableQty = availableQty
      //       record.availableQty -= record.frmlossQty
      //     }
      //   } else {
      //     record.availableQty = availableQty
      //     message.success('领用数量超出了可用量')
      //   }
      //   this.forceUpdate()
      // }}/>
    }
    ]
  }
  componentDidMount() {
    // 获取货主默认的值
    // const {handleGetSupplier, userLoation} = this.props
    // handleGetSupplier({warehouseCode:userLoation.value})
  }
  onInputChange(values) {
    const {popData, update} = this.props
    update({popData: {...popData, ...values}})
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowCreateModal: false})
  }
  // 新增报损单
  handleCommit = () => {
    const {
      handleCommit,
      createTable,
      userLoation,
      popData,
      frmlossTypeName,
      storeLocationCode,
      frmlossQty,
      frmlossQtyStute
      // nowFrmlossQty,
    } = this.props
    if (!popData.shipperCode) {
      message.error('请选择货主')
      return
    }
    if (!popData.frmlossTypeName) {
      message.error('请选择报损类型')
      return
    }
    if (!popData.costCenterName) {
      message.error('请选择成本中心')
      return
    }
    if (popData.shipperCode.value) {
      let req = {}
      req.items = JSON.parse(JSON.stringify(createTable)).filter((o, i) => o.productCode)
      if (req.items.length == 0) {
        message.error('请输入商品编码')
        return
      }

      if (storeLocationCode === undefined) {
        message.error('请输入储位编号')
        return
      }
      if (frmlossQty === undefined) {
        message.error('请输入报损数量')
        return
      }
      if (frmlossQtyStute === true) {
        message.error('报损数量不正确')
        return
      }
      req.items.map((o) => {
        o.productCode = o.productCode && o.productCode.value
        o.storeLocationCode = o.storeLocationCode && o.storeLocationCode.value
        o.frmlossQty = o.frmlossQty && o.frmlossQty.value
      })
      req.header = {
        costCenterName: popData.costCenterName.children,
        costCenterCode: popData.costCenterName.value,
        shipperName: popData.shipperCode.children,
        shipperCode: popData.shipperCode.value,
        frmlossTypeCode: popData.frmlossTypeName.value,
        frmlossTypeName: popData.frmlossTypeName.children,
        remark: popData.remark,
        warehouseCode: userLoation.value,
        warehouseName: userLoation.children
      }
      console.log(req)
      handleCommit({data: req}, () => {
        message.success('创建成功')
        const {handleGetList, searchParams, page, size, update} = this.props
        update({isShowCreateModal: false, createTable})
        handleGetList({searchParams: {page, size}})
      })
    }
  }
  render() {
    const {
      createTableLoading,
      createTable,
      popData: {
        frmlossTypeName,
        shipperCode,
        remark,
        costCenterName
      }
    } = this.props
    return (
      <Modal
        title="报损单创建"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={8}>
              <CsxSelect
                label='货主'
                require
                // antd={{ disabled: true }}
                dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  this.onInputChange({shipperCode: value})
                }}
              />
            </Col>
            <Col span={8}>
              <CsxSelect
                dateApi={API_GETREQUISITIONSTTYPEKEYVALUE}
                searchParams={{wmsOrderType: '库内报损'}}
                label='报损类型'
                require={true}
                selectItem={{data: frmlossTypeName, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  this.onInputChange({frmlossTypeName: value})
                }}
              />
            </Col>
            <Col span={8}>
              <CsxSelect
                dateApi={API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE}
                label='成本中心'
                require={true}
                selectItem={{data: costCenterName, key: 'key', valueKey: ['key', 'value']}}
                onChangeSelect={(value) => {
                  this.onInputChange({costCenterName: value})
                }}
              />
            </Col>
            <Col span={11}>
              <div className='commonInput'>
                <label>备注：</label>
                <div className='box'>
                  <Input value={remark} onChange={(e) => {
                    this.onInputChange({remark: e.target.value})
                  }} />
                </div>
              </div>
            </Col>
          </Row>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={createTableLoading}
              columns={this.columns}
              scroll={{x: 720, y: 400}}
              dataSource={createTable}
              pagination={false}
            />
            <Col>
              <p style={{color: 'red'}}>注: 称重单位（kg）可输入三位小数，非称重单位只能输入整数</p>
            </Col>
            <Row type="flex" justify="end" style={{marginTop: '15px'}}>
              <Col>
                <Button type="primary" onClick={this.handleCommit} >提交</Button>
              </Col>
            </Row>
          </div>
        </div>
      </Modal>
    )
  }
}



